@import url("https://fonts.googleapis.com/css2?family=Hahmlet:wght@100;300;500&family=Sora:wght@200;400&family=Source+Sans+Pro&display=swap");
/*Custom properties*/
:root {
  --dark-color: #1a191f;
  --purple-solid: #350a4f;
  --purple-transparent: rgba(53, 10, 79, 0.7);
  --purple-transparent-alt: rgba(53, 10, 79, 0.4);
  --purple-light: #8f50fb;
  --yellow-solid: #fa9e2c;
  --gradient-color: linear-gradient(
    to right,
    var(--yellow-solid),
    var(--purple-light)
  );
  --gradient-color-alt: linear-gradient(
    to right,
    var(--purple-light),
    var(--yellow-solid)
  );
  --nav-font: "Sora";
  --para-font: "Source Sans Pro", sans-serif;
}
* {
  margin: 0;
  padding: 0;
}
body {
  font-family: var(--nav-font);
  background-color: var(--dark-color);
  overflow-x: hidden;
}

/* Navigation bar */
.nav {
  width: 100%;

  height: 100px;
  /* justify-content: space-between; */
  position: absolute;

  background-color: #200a4f;
  display: flex;
  background: transparent;
  z-index: 10;
}
.logo {
  color: rgba(255, 0, 0, 0.487);
  /*   
  padding-left: 2rem;
  padding-top: 1.5rem; */
}
.logo h2 {
  font-family: var(--nav-font);
  font-size: 2rem;
  padding: 0.2rem 1rem;
  margin: 1.5rem 1rem;
  border-radius: 1rem;
  /* background-color: #e0e0e07c; */
}
.lists {
  display: flex;
  flex-direction: row;
  margin: auto;
  align-items: center;
  padding-left: 3rem;
}

.lists li {
  list-style: none;
  font-size: large;
  /* color: #e0e0e0; */
  color: red;
  font-family: var(--nav-font);
  /* padding-left: 4rem; */
  margin: auto;
  z-index: 2000;
}
.srch-btn {
  font-size: 30px;

  color: red;

  /* z-index: -1; */

  width: 3rem;

  position: relative;
  /* left: 32rem; */
  /* top: 0rem; */
}

/* Movies catagory */
.cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding-top: 2rem;
}
.card {
  width: 15rem;
  height: 20rem;
  position: relative;
  top: 0;
  left: 0;
  z-index: 10;
  padding: 1rem;
  justify-items: center;
}

.poster {
  width: 15rem;
  height: 20rem;
  position: absolute;
  z-index: -2;
  border-radius: 1rem;
}
.poster img {
  width: 15rem;
  height: 20rem;
  border-radius: 1rem;
}
.poster::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  /* background-color: var(--purple-transparent-alt); */
  background: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0) 10%,
    rgba(26, 25, 31, 1) 99%
  );
  z-index: 5;
  border-radius: 1rem 0 0 0;
}
/* .backdrop {
  width: 15rem;
  height: 20rem;
  position: absolute;
  z-index: -2;
  display: none;
  
  
  
} */
.card h4 {
  position: absolute;
  z-index: 10;
  top: 4.5%;
  left: 75%;
  color: #e0e0e0;
  font-family: var(--nav-font);
  font-size: 12px;
  background-color: red;
  border-radius: 8px;
  padding: 3px 5px;
}

.card span {
  position: absolute;
  z-index: 10;
  bottom: 5%;
  left: 80%;
  color: #e0e0e0;
  font-family: var(--para-font);
  font-size: 13px;
}
.card h3 {
  position: absolute;
  z-index: 10;
  bottom: 5%;
  left: 10%;
  width: 50%;
  color: #e0e0e0;
  font-family: var(--para-font);
  font-size: 13px;
}

/* Popular Movie slides */

.slide_item {
  width: 100%;
  height: 100vh;
}
.slide_item::before {
  content: "";
  position: absolute;
  top: 0%;
  left: 0;
  width: 100%;
  height: 100%;
  /* background-color: var(--purple-transparent-alt); */
  background: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0) 0%,
    rgba(26, 25, 31, 1) 99%
  );
  z-index: 2;
}

.slide_cont {
  color: #e0e0e0;
  position: absolute;
  top: 65%;
  left: 4%;
  z-index: 10;
  width: 40%;
}
.slide_cont span {
  display: inline-flex;
  padding: 0.5rem;
  font-family: var(--para-font);
}
.slide_cont li {
  padding-left: 1rem;
}
.slide_cont h2 {
  font-size: 2rem;
}
.slide_cont p {
  font-size: 17px;
  word-wrap: break-word;
  font-family: var(--para-font);
}
.slide_poster {
  width: 10rem;
  height: 20rem;
  position: absolute;
  top: 20%;
  right: 20%;
  z-index: 10;
  border-radius: 20px;
  box-shadow: 120px 130px 90px 100px rgba(0, 0, 0, 0.785),
    50px 50px 150px 60px rgba(255, 255, 255, 0.546);
}
.slide_poster img {
  object-fit: cover;
  width: 20rem;

  z-index: 10;
  border-radius: 20px;
}

/* Tv series Section */

.series {
  padding-top: 10rem;
}

.page-changer {
  position: relative;
  top: 10rem;
  justify-items: space-between;
  width: 100%;
  text-align: center;
}
button {
  width: 3rem;
  height: 1.5rem;
  color: red;
  background-color: transparent;
  border-style: none;
  margin: 0 20rem;
  /* padding: 2rem; */
}

.h2 {
  color: rgba(255, 255, 255, 0.393);
  font-family: var(--para-font);
  position: relative;
  top: 8rem;
  text-align: center;
}

/* Footer section */
.footer {
  width: 100%;
  height: 12rem;
  position: fixed;
  top: 100%;
}
.copy {
  color: rgba(255, 255, 255, 0.216);
  font-family: var(--para-font);
  font-size: 13px;
  font-weight: 100;
  position: relative;
  top: 80%;
  text-align: center;
}

/* Search section */
.srch-bar {
  width: 20rem;

  padding: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  /* position: relative; */
  left: 88.5rem;
  top: 0rem;
  z-index: 190;
  /* width: 5rem; */
}

.srch-bar input {
  height: 2.2rem;
  width: 15rem;
  border-radius: 17px;
  border-style: none;
  background-color: #e0e0e05d;
  text-decoration: none;
  border: none;
  font-family: var(--para-font);
  font-size: 1rem;
  color: var(--dark-color);
  font-weight: 300;

  text-indent: 25px;
}
.srch-bar input:focus {
  outline: none;
  text-align: left;
  text-indent: 20px;
}
.srch-bar button {
  font-size: 30px;
  padding-bottom: 2rem;
  margin: 0;
  background-color: var(--dark-color);
}

.disp {
  position: relative;
  top: 8rem;
  left: 0;
  margin-bottom: 5rem;
}
.srch-cont {
  width: 100%;
}
.srch-results {
